import React ,{useState}from 'react'
import Background from '../MIMYK LOGO_2019 - white version 2.png';

import { Icon, InlineIcon } from '@iconify/react';
import baselineArrowBackIosNew from '@iconify/icons-ic/baseline-arrow-back-ios-new';
import searchCircle from '@iconify/icons-heroicons-outline/search-circle';
import addOutline from '@iconify/icons-gridicons/add-outline';
import progressUpload from '@iconify/icons-mdi/progress-upload';
import infoCircle from '@iconify/icons-bi/info-circle';
import userAvatarFilledAlt from '@iconify/icons-carbon/user-avatar-filled-alt';


function Navbar() {

  const [search, setsearch] = useState(false);
  const [newappa,setnewappa]=useState(false);
  const [upload,setupload]=useState(false);
  const [info,setinfo]=useState(false);
  const [profile,setprofile]=useState(false);
  
  // const search=useRef(null);
  // const add=useRef(null);
  // const upload=useRef(null);
  // const info=useRef(null);
  // const profile=useRef(null);



   
    return (
<div>          
<nav class="navbar navbar-expand-lg navbar-light" style={{ backgroundColor: "#1D99D6",height:"40px" }}>
  <button  style={{ backgroundColor:'rgba(0,0,0,0)',border:"none" }}>
<Icon  icon={baselineArrowBackIosNew} style={{color: '#ffffff', fontSize: '24px',cursor:"pointer"}} /></button>
  <a style={{ margin:"0 0 6px 2%" }} className="navbar-brand" href="#"><img src={Background} height="30px" alt=""></img></a>
  <div  id="navbarSupportedContent">
    <ul style={{ marginLeft:"1020px" }}className="navbar-nav mr-auto">
      <li className="nav-item active" >
        <a className="nav-link" href="#"><Icon onMouseEnter={() => setsearch(true)} onMouseLeave={() => setsearch(false)} icon={searchCircle} style={{color: '#ffffff', fontSize: '25px' }} />
        {/* <span className="menuText" > Click to start Search</span> */}
        {search && (
        <span className="menuText" style={{ right:"260px" }}>
           Click to start Search
        </span>
      )}
        </a>
      </li>
      <li className="nav-item active">
        <a className="nav-link" href="#"><Icon onMouseEnter={() => setnewappa(true)} onMouseLeave={() => setnewappa(false)} icon={addOutline} style={{color: '#ffffff', fontSize: '25px',marginLeft:"20px" }} />
        {newappa && (
        <span className="menuText" style={{ right:"190px" }}>
           New Appointement
        </span>
      )}
        </a>
      </li>
      
      <li className="nav-item active">
        <a   className="nav-link" href="#"><Icon onMouseEnter={() => setupload(true)} onMouseLeave={() => setupload(false)} icon={progressUpload} style={{color: '#ffffff', fontSize: '25px',marginLeft:"20px"}} />
        {upload && (
        <span className="menuText" style={{ right:"130px" }}>
           Upload exsisting exam
        </span>
      )}
        </a>
      </li>
      <li className="nav-item active">
        <a    className="nav-link" href="#"><Icon onMouseEnter={() => setinfo(true)} onMouseLeave={() => setinfo(false)}  icon={infoCircle} style={{color: '#ffffff', fontSize: '20px',marginLeft:"20px"}} />
        {info && (
        <span className="menuText" style={{ right:"90px" }}>
          Info and Help
        </span>
      )}
        </a>
      </li>
      <li className="nav-item active">
        <a   className="nav-link" href="#"><Icon onMouseEnter={() => setprofile(true)} onMouseLeave={() => setprofile(false)} icon={userAvatarFilledAlt} style={{color: '#ffffff', fontSize: '22px',marginLeft:"20px"}} />
        {profile && (
        <span className="menuText" style={{ right:"40px" }}>
           user Profile
        </span>
      )}
        </a>
      </li>
    </ul>
    
  </div>
</nav>

 </div>
    )
}

export default Navbar

