import React,{useState,useRef} from 'react'
import { Icon, InlineIcon } from '@iconify/react';
import tickCircleSolid from '@iconify/icons-teenyicons/tick-circle-solid';
import triangle11 from '@iconify/icons-maki/triangle-11';
import { getQueriesForElement } from '@testing-library/react';


function Entries({id,consultant,details,name, age, gender, phone,refferedby, date,time,clicked,setclicked,arr,setarr}) {
  
  const [active,setactive]=useState(false)

  const test=
  (
    <>
    {<span className ="day">{date} <Icon icon={triangle11} style={{fontSize: '10px',color:'#1D99D6',marginLeft:"5px", transform:"rotate(180deg)" }} /></span>}
  <tr>
  <td> <Icon icon={tickCircleSolid} style={{color: '#bbbbbb', fontSize: '12px',marginRight:"10px"}} />{id}</td>
  <td>{name}{gender} {age}</td>
  <td>{details}</td>
  <td>{consultant}</td>
  <td>{refferedby}</td>
  <td style = {{ color: "#1d99d6" }}>{time}</td>
  </tr>
  </>
  );
  
  
  function add1()
  { 
    if (clicked =='1')
    {
    setarr(arr.concat(test))
    setclicked(0)
  }
}

function ticked(){
  alert('again')
  setactive(!active);
}

  return (
        <div style={{ marginTop:"8%",width:"90%",marginLeft:"6%" }}>
            <table className="table table-borderless" style = {{ backgroundColor: "rgba(0,0,0,0)"  }}>
  <thead>
    <tr style={{ borderBottom: "1px solid black" }}>
      <th scope="col">
      <Icon className="greenit" onClick={ticked} icon={tickCircleSolid}  style={{color: '#bbbbbb', fontSize: '12px',marginRight:"10px" }} /> Patient ID
      </th>
      <th scope="col">Patient details</th>
      <th scope="col">Procedure details</th>
      <th scope="col">Consultant</th>
      <th scope="col">Referred by</th>
      <th scope="col">Time</th>
    </tr>
  </thead>
  <tbody id="test1">  
  {add1()}
  {arr}
  </tbody>

</table>
        </div>
    )
}

export default Entries
